import {useState} from 'react'

const Navbar = ({is_logged}) => {
    const  [class_name,setclassname]  = useState('inactive')
    const  [style,setstyle] = useState({
            'left': '0px',
            'top': '0px'
    })


    function eventHandler(){
        if(class_name==='inactive'){
            setclassname('')
            setstyle({
                'left': '400px',
                'font-size':'70px'
            })
        }else{
            setclassname('inactive')
            setstyle({
                'left': '0px',
                'font-size':'50px'
            })
        }
    }
    
    return (
    <div>
        <p className='toggle' style={style} onClick={(e) => eventHandler()}>{class_name==='inactive' ? '☰' : '×'}</p>
        <div id="sidebar" className={class_name}>
        <div class="inner">
        <section id="search" class="alt">
        </section>
        <nav id="menu">
            <ul>
                <li><a href="/">Homepage</a></li>
                <li><a href="/universities">Universities Stats</a></li>
                <li><a href="/lecturers">Lecturers Stats</a></li>
                {!is_logged && <li><a href="/login">Login or Register</a></li> }
                {is_logged && <li><a href="/profile">Profile</a></li>}
            </ul>
        </nav>
        <footer id="footer">
              <p class="copyright">
              <strong>Created by: </strong><br />
              <br /><strong>  Bohdan Mahometa  </strong><br />
              <br /><strong>  Fedir Zhydok  </strong><br />
              <br /><strong>  Yaroslav Brovchenko  </strong><br />
              <br /><strong>  Viktor Povazhuk  </strong><br />
              <br /><strong>  Nikita Ishchenko  </strong></p>
        </footer>
        </div>
        </div>
    </div>
    )
}

export default Navbar
