import React from 'react'

const Main = ({data}) => {
    return (
        <div className='main'>
            <div class="inner">


<section class="main-banner">
  <div class="container-fluid">
    <div class="row">
      <div class="col-md-12">
        <div class="banner-content">
          <div class="row">
            <div class="col-md-12">
              <div class="banner-caption">
                <h4>Привіт, це ресурс <em>Unistats</em></h4>
                <span>Статистика для Університетів &amp; Студентів &amp; Абітурієнтів</span>
                <p><strong>Наша ціль: </strong>
Мета <em>UNISTATS</em> ліквідувати “сліпе” вибирання абітурієнтом ВНЗ та студентом предметів вільного вибору. Нині відсутнє централізоване, достовірне джерело інформації про те, що насправді відбувається в ВНЗ, чому там вчать, та наскільки те, чому вчать актуально. Але тепер є <em>UNISTATS.</em></p>
                <div class="primary-button">
                  <a href="/universities">Proceed</a>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</section>



<section class="services">
  <div class="container-fluid">
    <div class="row" >
      <div class="col-md-4">
        <div class="service-item">
          <div class="icon"><img src="https://img.icons8.com/ios/100/000000/reading.png" alt=""/></div>
          <h4>Абітурієнтам</h4>
          <p>Якщо <strong>Ви - абітурієнт</strong>, то забудьте про перелопачення тон інформації на сайтах, в різних чатах, групах, соцмережах, щоб знайти навчальний заклад, який дійсно відповідає Вашим бажанням та очікуванням. На <strong><em style={{"color": "rgb(83, 98, 255)"}}>UNISTATS</em></strong> Ви знайдете всю дійсно важливу, актуальну статистику та навіть більше: реальні відгуки та оцінки студентів про викладачів та заголом атмосферу в закладі.</p>
        </div>
      </div>
      <div class="col-md-4">
        <div class="service-item">
        <div class="icon"><img src="https://img.icons8.com/ios/100/000000/graduation-cap.png" alt=""/></div>
          <h4>Студентам</h4>
          <p>Якщо <strong>Ви - студент</strong>, то забудьте про вгадування під час обрання предметів вільного вибору. На нашому ресурсі Ви можете не лише переглядати відгуки інших, а й заповнювати свої. <br/> Таким чином Ви заробляєте свій рейтинг на <strong><em style={{"color": "rgb(83, 98, 255)"}}>UNISTATS</em></strong>. Будьте максимально об'єктивними при оцінці, адже Ваш фідбек будуть оцінювати інші користувачі.</p>
        </div>
      </div>
      <div class="col-md-4">
        <div class="service-item">
          <div class="icon"><img src="https://img.icons8.com/ios/100/000000/library.png" alt=""/></div>
          <h4>Університетам</h4>
          <p>Якщо <strong>Ви - Університет</strong>, то Ви можете переглядати відгуки про Ваш та інші ВНЗ. Тут Ви знайдете конструктивні зауваження та компліменти.
Якщо Ви бажаєте, щоб на сторінці <strong><em style={{"color": "rgb(83, 98, 255)"}}>UNISTATS</em></strong> зі статистикою Вашого університету були посилання на Ваші сторінки в соцмережах чи офіційний сайт, пропонуємо Вам купити підписку. Для її купівлі - заповніть форму нижче.</p>
        </div>
      </div>
    </div>
  </div>
</section>

<section class="left-image">
  <div class="container-fluid">
    <div class="row">
      <div class="col-md-6">
        <iframe src='/media/6' title='university' width='100%' height='400px' padding='0px' margin='0px auto'/>
      </div>
      <div class="col-md-6">
        <div class="right-content">
          <h4>University Stats</h4>
          <p>Many universities apply to be on our resource and give information about lecturers, faculties, etc.. In response, we provide relevant stats about position of specific university, its ranking and enrolee's rates. With data we provide, university will be able to increase number of enrolee's and ranking among other competitors.</p>
          <div class="primary-button">
            <a href="/universities">Check Out</a>
          </div>
        </div>
      </div>
    </div>
  </div>
</section>

<section class="right-image">
  <div class="container-fluid">
    <div class="row">
      <div class="col-md-6">
        <div class="left-content">
          <h4>Lecturers Stats</h4>
          <p>Also we provide data about lecturer's themselves, we have many criterias which will be plotted in beautiful statistical image. Students, which have learnt from specific lecturer will be able to leave their rate and feedback of his work </p>
          <div class="primary-button">
            <a href="/lecturers">Check Out</a>
          </div>
        </div>
      </div>
      <div class="col-md-6">
      <iframe src='/media/6' title='university' width='100%' height='400px' padding='0px' margin='0px auto'/>
      </div>
    </div>
  </div>
</section>


<section class="services">
  <div class="container-fluid">
    <h2 style={{'margin':'50px auto'}}>Використані технології</h2>
    <div class="row" >
      <div class="col-md-4">
        <div class="service-item fourth-item ">
          <div class="icon"></div>
          <h4>Front-end</h4>
          <p>Technologies: <strong>React</strong>, <strong>Bootstrap</strong>, <strong>JQuery</strong>, <strong>HTML</strong>, <strong>CSS</strong>, <strong>JavaScript</strong></p>
        </div>
      </div>
      <div class="col-md-4">
        <div class="service-item first-item ">
          <div class="icon"></div>
          <h4>Back-end</h4>
          <p>Technologies: <strong>Python</strong>, <strong>Flask</strong>, <strong>PostgreSQL</strong></p>
        </div>
      </div>
      <div class="col-md-4">
        <div class="service-item third-item">
          <div class="icon"></div>
          <h4>Data Visualization</h4>
          <p>Technologies: <strong>MatPlotLib</strong>, <strong>Dash</strong></p>
        </div>
      </div>
    </div>
  </div>
</section>
<section className="forms">
              <div className="container-fluid">
                <div className="row">
                  <div className="col-md-12">
                    <div className="section-heading">
                      <h2>Замовити підписку для ВНЗ</h2>
                    </div>
                    <form id="contact" action="" method="post">
                      <div className="row" style={{'width':'70%', 'margin':'20px auto' ,'backgroundColor':'rgba(83,91,160,1)'}}>
                        <div className="col-md-6" style={{'width':'51%', 'margin':'20px auto', 'margin-top':'50px'}}>
                          <fieldset>
                            <input name="name" type="text" className="form-control" id="name" placeholder="Контактна особа" required="" />
                          </fieldset>
                        </div>
                        <div className="col-md-6" style={{'width':'51%', 'margin':'0px auto', 'margin-bottom':'0px'}}>
                          <fieldset>
                            <input name="email" type="email" className="form-control" id="email" placeholder="Офіційна пошта" required="" />
                          </fieldset>
                        </div>
                        <div className="col-md-12" style={{'margin':'50px auto'}}>
                          <div className="border-radius-button"><button type='submit' style={{'backgroundColor':'#eee'}}>Зв'язатись з нами</button></div>
                        </div>
                      </div>
                    </form>
                  </div>
                </div>
              </div>
            </section>


    </div>
  </div>
    )
}

export default Main
