import './App.css';
import {
  BrowserRouter as Router,
  Switch,
  Route
} from "react-router-dom";
import Header from './components/Header'
import Universities from './components/Universities'
import Profile from './components/Profile'
import Registration from './components/Registration'
import Main from './components/Main'
import Lecturers from './components/Lecturers'
import Navbar from './components/Navbar'
import Login from './components/Login'
import University from './components/University'
import LecturerContest from './components/LecturerContest'
import OverallContest from './components/OverallContest'
import Contests from './components/Contests'
import Lecturer from './components/Lecturer'
import SuccessLogin from './components/Success_login'
import UnsuccessLogin from './components/Unsuccess_login'

function App({data}) {
  
  return (
      <Router>
      <div className="App" id="wrapper">
        <div id="main">
          <Header />
          <Navbar is_logged={data['is_logged']}/>
              <Switch>
                <Route path="/lecturer_:id" render={()=><Lecturer data={data}/>}/>
                <Route path="/university_:id" render={()=><University data={data}/>} />
                <Route path="/survey_:id" render={()=><OverallContest data={data}/>} />
                <Route path="/lecturersurvey_:id" render={()=><LecturerContest data={data}/>} />
                <Route exact path="/">
                  <Main data={data}/>
                </Route>
                <Route exact path="/surveys">
                  <Contests data={data}/>
                </Route>
                <Route exact path="/registration">
                  <Registration data={data}/>
                </Route>
                <Route exact path="/profile">
                  <Profile data={data}/>
                </Route>
                <Route exact path="/universities">
                  <Universities data={data}/>
                </Route>
                <Route exact path="/lecturers">
                  <Lecturers data={data}/>
                </Route>
                <Route exact path="/login">
                  <Login data={data}/>
                </Route>
                <Route exact path="/surveys">
                  <Contests data={data}/> 
                </Route>
                <Route exact path="/success_login">
                  <SuccessLogin /> 
                </Route>
                <Route exact path="/unsuccess_login">
                  <UnsuccessLogin /> 
                </Route>
              </Switch>
          </div>
        </div>
      </Router>
  );
}



export default App;
