import React from 'react'

const Aspect = ({index, criteria}) => {
    let counters = [1+parseInt(index)*5,2+parseInt(index)*5,3+parseInt(index)*5,4+parseInt(index)*5,5+parseInt(index)*5]
    console.log(index)
    return (
        <div className='stars in_wrapper'>
            <h2>{criteria}</h2>
            <div className='wrapper'>
                {  
                    counters.map(ind => <div><input type="radio" id={"r"+ind} key={ind} value={6-ind%5===6 ? 1 : 6-ind%5} name={criteria} /><label htmlFor={"r"+ind}>&#9733;</label></div>)
                }
            </div>
        </div>
    )
}

export default Aspect
