import Aspect from './Aspect'


const OverallContest = () => {
    let ideals = ['General rate', 'Sufficiency rate', 'Relevance rate', 'Loyalty rate', 'Politeness rate', 'Material rate', 'Punctuality rate', 'Objectivity rate', 'Adaptation rate', 'Complexity rate']
    return (
        <div >
            <form method='post'>
            {  
                ideals.map((criteria , index) => <Aspect index={index} criteria={criteria} /> )
            }
            <button type="Submit">Submit</button>
            </form>
        </div>
    )
}

export default OverallContest