import React from 'react'
import {useParams} from 'react-router-dom'

const Lecturer = ({data}) => {
    let imitated_data ={
        'name':'Заєць Анатолій Павлович',
        'description':"доктор юридичних наук, професор, член-кореспондент Національної академії правових наук України, Заслужений юрист України. \
        Основні напрями наукових досліджень: теоретико-понятійне осмислення права, вивчення його природи, процесу правотворення, встановлення співвідношення права і закону, дослідження взаємозв’язків права і держави та ролі права в формуванні і функціонуванні \
        держави і суспільства; теоретико-прикладні аспекти  формування правової і соціальної держави та верховенства права в Україні, проблеми становлення українського парламентаризму, організація та діяльність місцевого самоврядування, дослідження форми правління, \
        здійснення принципу поділу влади; конституційне регулювання та механізми захисту прав і свобод людини і громадянина.",
        'position':'завідувач кафедри загальнотеоретичного правознавства та публічного права',
        'courses':['Право у врядуванні', 'Методологія правознавства', 'Права людини та їх захист'],
        'url_photo':'https://law.ukma.edu.ua/wp-content/uploads/2018/12/Zaiets.jpg',
        'url_page':"https://law.ukma.edu.ua/specialists/zayets-anatolij-pavlovych/"
    }
    let params = useParams()
    console.log(params)
    console.log("/lecturersurvey_"+params.id)
    return (
      <div className='lecturer'>
        <section class="left-image">
                <div class="container-fluid">
                <div class="row">
                    <div class="col-md-6">
                    <img src={imitated_data.url_photo} alt="" />
                    </div>
                    <div class="col-md-6">
                    <div class="right-content" style={{'margin-top':'40px'}}>
                        <h4>{imitated_data.name}</h4>
                        <h5>{imitated_data.position}</h5>
                        <p>{imitated_data.description}</p>
                        <div style={{'margin':'20px', 'width':'100%', 'float':'left'}} className='courses'>
                            {imitated_data.courses.map(course => <div className='filled-icon-button' style={{'margin':'20px', 'width':'40%', 'float':'left'}}><a>{course}</a></div>)}
                        </div>
                        <div className="primary-button">
                        <a href={imitated_data.url_page} style={{'margin':'10px'}}> Original Page </a>
                        </div>
                    </div>
                    </div>
                </div>
                </div>
        </section>
        <section class="top-image">
        <div class="container-fluid">
          <div class="row">
            <div class="col-md-8" style={{"margin":'50px auto'}}>
              <img src="assets/images/top-image.jpg" alt="" />
              <div class="down-content">
                <h4>Stats</h4>
                <p>Waiting for stats.....</p>
              </div>
            </div>
          </div>
        </div>
      </section>
        <div className="col-md-6" style={{'margin':'0px auto'}}>
            <div className="filled-rectangle-button">
                <a  href={"/lecturersurvey_"+params.id}> Survey </a>
            </div>
        </div>
      </div>
    )
}

export default Lecturer
